body{
  font-family: 'Open Sans Condensed', sans-serif;
  padding:20px 80px;
}
a{
  text-decoration: none;
  color: black;
}
.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 80px;
}
.directory-menu {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.menu-item {
  min-width: 30%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 7.5px 15px;
}
.menu-item :hover{
  cursor:pointer;
}
.menu-item:hover .background-image{
  transform: scale(1.1);
  transition: transform 6s cubic-bezier(.25,.45,.45,.95);
}
.menu-item:hover  .content {
  opacity:.9;
}
.menu-item .background-image{
  width:100%;
  height:100%;
  background-size: cover;
  background-position: center;
}
.large{
  height:380px;
}
.menu-item:first-child {
  margin-right: 7.5px;
}
.menu-item:last-child {
  margin-left: 7.5px;
}
.menu-item .content {
  height: 90px;
  padding: 0 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  background: #fff;
  opacity: 0.7;
  position: absolute;
}

.menu-item .content .title {
  font-weight: bold;
  margin-bottom: 6px;
  font-size: 22px;
  color: #4a4a4a;
}
.menu-item .content .subtitle {
  font-weight: lighter;
  font-size: 16px;
}.collection-preview {
   display: flex;
   flex-direction: column;
   margin-bottom: 30px;
 }
.collection-preview .title {
  font-size: 28px;
  margin-bottom: 25px;
}
.collection-preview .preview {
  display: flex;
  justify-content: space-between;
}
.collection-item {
  width: 22%;
  display: flex;
  flex-direction: column;
  height: 350px;
  align-items: center;
}
.collection-item .image {
  width: 100%;
  height: 95%;
  background-size: cover;
  background-position: center;
  margin-bottom: 5px;
}
.collection-item .collection-footer {
  width: 100%;
  height: 5%;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}
.collection-item .collection-footer .name {
  width: 90%;
  margin-bottom: 15px;
}
.collection-item .collection-footer .price {
  width: 10%;
}

.header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.header .logo-container {
  height: 100%;
  width: 70px;
  padding: 25px;
}

.header  .options {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.header  .option {
  padding: 10px 15px;
}

